@import "tailwindcss/base";
@import "tailwindcss/components";
@import "tailwindcss/utilities";

* {
  box-sizing: border-box;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

:root {
  --page-control-duration-time: 2.8s;
}

html {
  --font-display: -apple-system, "SF Pro Text", "SF UI Text", system-ui,
    "Helvetica Neue", Helvetica, Arial, sans-serif;
  --tg-theme-section-bg-color: var(--tg-theme-bg-color);
  --tg-theme-subtitle-text-color: var(--tg-theme-hint-color);
  --tg-theme-section-header-text-color: var(--tg-theme-link-color);
  --tg-theme-accent-text-color: var(--tg-theme-link-color);
  --tg-theme-destructive-text-color: #f16d66;
  --separator-color: #c8c7cb;
  --second-button-color: rgba(0, 122, 255, 0.1);
  --text-confirm-color: #34c759;
  --button-confirm-color: #34c759;
  --button-main-disabled-color: #e8e8e9;
  --text-main-disabled-color: #b9b9ba;
  --button-destructive-color: rgba(255, 59, 48, 0.1);
  --highlight-default: hsla(240, 2%, 56%, 0.24);
  --quick-menu-background: hsla(0, 0%, 100%, 0.55);
  --quick-menu-foreground: rgba(0, 0, 0, 0.1);
  --toast-background: rgba(45, 45, 45, 0.8);
  --text-overlay: #fff;
  --toast-link: #5ac8fa;
  --tertiary-fill-background: hsla(240, 5%, 48%, 0.12);
  --quaternary-fill-background: hsla(240, 5%, 48%, 0.08);
  --separator-non-opaque-color: rgba(60, 60, 67, 0.36);
  --accent-orange: #ff9500;
  --accent-brown: #ac8e68;
  --segmented-control-active-background: #fff;
  --tooltip-background: rgba(0, 0, 0, 0.8);
}

html {
  height: 100%;
}

body {
  font-family: var(--font-display);
  font-weight: 400;
  color: #fff;
  // background-color: #000;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  margin: 0;
  -webkit-overflow-scrolling: touch !important;
  user-select: none;
}

.light {
  background: #fff;
}

.dark {
  background: #000;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

* {
  box-sizing: border-box;
}

button {
  cursor: pointer;
  border: none;
  background: transparent;
  font-family: var(--font-display);
}
button:disabled {
  cursor: not-allowed;
}

a {
  color: #8774e1;
  text-decoration: none;
}
svg {
  display: block;
}

img {
  display: initial;
}

[contenteditable],
input,
textarea {
  caret-color: #8774e1;
  -webkit-user-select: initial;
  -moz-user-select: initial;
  user-select: auto;
}

.page {
  position: relative;
  &__content {
    min-height: 100vh;
  }
}

.scrolling {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-touch-callout: none;
  user-select: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

.container {
  margin: 0 auto;
  max-width: 100% !important;
}

.text-shadow {
  text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.08);
}

