.popup-web-app-icon {
  position: absolute;
  width: 5rem;
  height: 5rem;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
  opacity: 1;
}

.popup-web-app-icon .media-photo {
  width: 100%;
  height: 100%;
}

.media-photo,
.media-video,
.media-sticker,
.media-round,
.media-poster {
  position: absolute;
  inset: 0;
}
