.progress-bar {
  border: none;
  height: 16px;
  border-radius: 24px 24px 0 0;
  overflow: hidden;
  background-color: #fff3;
  width: 100%;
  flex-grow: 1;
}
.progress-bar-line {
  height: 16px;
  border-radius: 24px 24px 0 0;
  width: 100%;
  transition: width 0.45s ease;
  opacity: 1;
  animation: progress-bar-opacity 1s forwards;
  will-change: opacity, width, transition;
}

@keyframes progress-bar-opacity {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.progress-bar-color {
  background: linear-gradient(89deg, #2b82eb, #1cc8ff);
}
