.tab {
  height: 66px;
  width: 100%;
  background-color: transparent;
  transition: background-color ease;
  transition-duration: 0.3s;
  &:active {
    transition-duration: 0s;
    background-color: rgba(235, 235, 245, 0.4);
  }
  &-frens {
    border-radius: 16px 0 0 16px;
  }
  &-sparkles {
    border-radius: 0 16px 16px 0;
  }
}
